<template>
  <div class="Tabber">
    <mt-tabbar v-model="selected" class="bottom">
      <mt-tab-item id="home">
        <img slot="icon" src="../assets/img/home.png" />
        首页
      </mt-tab-item>
      <mt-tab-item id="category">
        <img slot="icon" src="../assets/img/personal.png" />
        分类
      </mt-tab-item>
      <mt-tab-item id="shopCart">
        <img slot="icon" src="../assets/img/add-cart.png" />
        购物车
      </mt-tab-item>
      <mt-tab-item id="user">
        <img slot="icon" src="../assets/img/user.png" />
        我的
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
// 引入Tabar
import Vue from "vue";
import { Tabbar, TabItem } from "mint-ui";
Vue.component(Tabbar.name, Tabbar);
Vue.component(TabItem.name, TabItem);
export default {
  name: "Tabber",
  data() {
    return {
      selected: ""
    };
  },
  watch: {
    selected(val) {
      switch (val) {
        case "home":
          this.$router.push("./home");
          break;
        case "category":
          this.$router.push("./category");
          break;
        case "shopCart":
          this.$router.push("./shopCart");
          break;
        case "user":
          this.$router.push("./user");
          break;
      }
    }
  }
};
</script>

<style>
.Tabber{
  height: 55px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}
.Tabber .bottom{
  
  height: 55px;
}
</style>