//创建本地存储的存值和取值函数
function setItem(name, item) {
  localStorage.setItem(name, JSON.stringify(item))
}
//取值 
function getItem(name) {
  return JSON.parse(localStorage.getItem(name) || '[]')
}
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)



export default new Vuex.Store({
  state: {
    // cartList:[]
    cartList: getItem('cart')
  },
  getters: {
    //根据id获取商品的数量
    getCount(state) {
      let s = {}
      state.cartList.forEach(item => {
        s[item.id] = item.count
      })
      return s
    },
    //根据id获取商品的bool值
    getBool(state) {
      let s = {}
      state.cartList.forEach(item => {
        s[item.id] = item.bool
      })
      return s
    },
    //算出勾选的商品总数
    getCountBoolAll(state) {
      var n = 0;
      state.cartList.some(item => {
        if (item.bool == true) {
          n += item.count
        }
      })
      return n;
    }
  },
  mutations: {
    cartadd(state, param) { //param 我们要添加的商品
      // state.cartList.push(param)
      var flag = false
      state.cartList.some(item => { //循环购物车
        if (item.id == param.id) { //判断购物车中已经存在的商品id和即将加入购物和的商品id
          item.count += parseInt(param.count) //id相同就把数量进行相加
          flag = true
          return
        }
      })
      if (!flag) { //flag值为true，就代表数组中没有相同的id就把数据添加到购物车
        state.cartList.push(param)
      }

      //把购物车中的数据存储到本地
      setItem('cart', state.cartList) //调用存值函数

    },
    changeBool(state, obj) { //修改购物车中的bool值
      state.cartList.some(item => {
        if (item.id == obj.id) {
          item.bool = obj.bool //将bool值复制给本地存储
          return
        }
      })
      //把修改好的数据存到本地存储
      setItem('cart', state.cartList);
    },
    changeCount(state, obj) { //修改单个商品的数量递增或递减
      state.cartList.some(item => {
        if (item.id == obj.id) {
          if (obj.sub2add) {
            item.count++
          } else {
            item.count--
          }
        }
      })
    },
    changeDel(state, idval) { //删除
      state.cartList.some((item, i) => {
        if (item.id == idval) {
          //删除的是本地存储的数据
          state.cartList.splice(i, 1)
          return
        }
      })
      //把数据保存到本地
      setItem('cart', state.cartList)
    },
  }
})