<template>
  <div class="Banner">
   <div class="swipe-wrapper">
      <mt-swipe :auto="2000">
        <mt-swipe-item class="swip-item-1 item" v-for="(item,i) in list" :key="i">
          <img :src="item.img" alt />
        </mt-swipe-item>
      </mt-swipe>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
// 轮播
import { Swipe, SwipeItem } from "mint-ui";
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
export default {
  name: "Banner",
  data() {
    return {
      list:null
    }
  },
  mounted() {
    this.$axios
    .get('http://47.93.224.9/api/imglist')
    .then(res => (this.list = res.data.data))
    .catch(error => {console.log(error)})
  },
};
</script>

<style>
.Banner img {
  width: 100%;
  height: 100%;
}
html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
}
.Home {
  width: 100%;
  height: 100%;
}
.swipe-wrapper {
  width: 100%;
  height: 200px;
}
.swip-item-1 {
  background: red;
}
.swip-item-2 {
  background: blue;
}
.swip-item-3 {
  background: green;
}
.item {
  text-align: center;
  font-size: 40px;
  color: white;
}
</style>