<template>
  <div id="app">
    <div class="header">
      <mt-header fixed :title="$route.meta.title">
        <span slot="left" v-show="showBack">
          <mt-button icon="back" @click.native="$router.go(-1)">返回</mt-button>
        </span>
      </mt-header>
    </div>
    <router-view />
    <Tabber />
  </div>
</template>

<script>
import Tabber from "./components/Tabber";
export default {
  name: "App",
  components: {
    Tabber
  },
  data(){
    return{
      showBack:false
    }
  },
  created() {
    this.showBack=this.$route.path !== '/home'
  },
  watch: {
    '$route.path'(newVal){
      this.showBack=newVal !== '/home'
    }
  },
};
</script>
<style>
@import url("./assets/css/base.css");
